@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pageSlider-exit > .page {
  z-index: 1;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  overflow: hidden;
}

.pageSlider-exit-active > .page {
  opacity: 0.01;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 300ms;
}

.pageSlider-enter > .page {
  z-index: 10;
  opacity: 0.01;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  overflow: hidden;
}

.pageSlider-enter-active > .page {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  transition: all 300ms;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
}

body {
  height: auto;
  background-color: rgb(10, 25, 47);
}

.page {
  position: absolute;
  width: 100vw;
  height: 100vh;
}

.content {
  width: 80%;
  height: auto;
  margin: auto;
  color: rgb(136, 146, 176);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.section {
  position: relative;
  min-height: 90vh;
  height: auto;
  padding-top: 10vh;
  margin: 0 10vh;
}
.section hr:not([type=connection]) {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

footer {
  background-color: rgb(2, 12, 27);
  height: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
footer a {
  color: rgb(136, 146, 176);
}
footer a:hover {
  color: turquoise;
}

.title {
  margin-top: 25px;
  color: turquoise;
}

.flashy-link {
  display: inline-block;
  border: thin solid turquoise;
  background-color: transparent;
  color: turquoise;
  padding: 0.75em 1.5em;
  border-radius: 5px;
  margin-left: 3%;
  transition: 0.25s;
}

.flashy-link:hover {
  background: rgba(64, 224, 208, 0.1);
}

@media screen and (max-width: 480px) {
  html, body {
    overflow-x: hidden;
  }
  body {
    position: relative;
  }
  .content {
    width: 100%;
  }
  .section {
    margin: 0 5vh;
  }
}
.sidebar {
  display: inline;
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 25px;
  margin-bottom: 5vh;
}

.logo {
  margin: 15px;
  width: 30px;
  height: 30px;
  transition-duration: 0.25s;
}
.logo path {
  fill: rgb(136, 146, 176);
}

.logo:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
.logo:hover path {
  fill: turquoise;
}

@media screen and (max-width: 480px) {
  .sidebar {
    position: static;
    flex-direction: row;
    width: 100%;
    margin-bottom: 0;
    padding: 0;
  }
}
.nav-bar {
  width: 100%;
  background-color: rgb(2, 12, 27);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 10vh;
  padding: 15px 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  z-index: 100;
  box-shadow: 0 8px 6px -6px rgba(0, 0, 0, 0.4);
}

#home {
  width: 50px;
  height: 50px;
  background-image: url(/static/media/logo.b7d3bfed.jpg);
  background-size: cover;
  margin-right: auto;
}

.nav-burger {
  display: none;
}

.nav-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

ol {
  width: 40%;
  display: inline-block;
  font-size: 1.25em;
}
ol li {
  color: turquoise;
}
ol li a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: turquoise;
  visibility: hidden;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  transition: all 0.3s ease-in-out 0s;
}
ol li a {
  position: relative;
  margin-left: 10px;
  color: rgb(136, 146, 176);
}
ol li a:hover {
  color: turquoise;
}
ol li a:hover::before {
  visibility: visible;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.nav-resume {
  margin-left: 5vw;
  margin-right: 1vw;
  position: relative;
  display: inline-block;
}

.nav-resume-header {
  padding: 10px;
  font-size: 1.25em;
  color: rgb(136, 146, 176);
}

.nav-resume-header:hover {
  color: turquoise;
}

.nav-resume-content {
  width: 100%;
  margin-top: 10px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  overflow: hidden;
  background-color: rgb(23, 42, 69);
  transition: all 0.5s ease;
}
.nav-resume-content a {
  display: block;
  color: rgb(136, 146, 176);
  padding: 12px;
  text-decoration: none;
}
.nav-resume-content a:hover {
  color: turquoise;
}

.nav-resume:hover .nav-resume-content {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 480px) {
  #home {
    display: none;
  }
  .nav-burger {
    color: rgb(136, 146, 176);
    margin-left: 6%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
  .nav-burger:hover {
    color: turquoise;
  }
  .nav-burger:hover + .nav-links {
    opacity: 1;
    visibility: visible;
  }
  .nav-links {
    visibility: hidden;
    display: block;
    flex-direction: column;
    position: absolute;
    top: 10vh;
    left: 0;
    background-color: rgb(23, 42, 69);
    opacity: 0;
    transition: all 0.5s ease;
  }
  .nav-links li {
    list-style-type: none;
    margin: 3%;
  }
}
#intro-greeting, #intro-name, #intro-statement, #intro-description, #intro-contact-me {
  margin: 25px 0;
}

#intro-greeting {
  color: turquoise;
  font-size: 1em;
}

#intro-name {
  color: lavender;
  font-size: 5em;
}

#intro-statement {
  color: rgb(136, 146, 176);
  font-size: 4em;
}

#intro-description {
  width: 60vw;
  color: rgb(136, 146, 176);
  font-size: 1em;
  line-height: 1.5em;
  letter-spacing: 2px;
  margin-bottom: 3em;
}

#intro-contact-me {
  font-size: 1.5em;
}

@media screen and (max-width: 480px) {
  #intro-greeting, #intro-name, #intro-statement, #intro-description, #intro-contact-me {
    margin: 7px 0;
  }
  #intro-greeting {
    font-size: 0.85em;
  }
  #intro-name {
    font-size: 3em;
  }
  #intro-statement {
    font-size: 2em;
  }
  #intro-description {
    width: 100%;
    margin: 6vh 0;
    font-size: 0.9em;
  }
  #intro-contact {
    display: flex;
    width: 100%;
  }
  #intro-contact-me {
    font-size: 1.5em;
    margin: auto;
  }
}
#about-body {
  display: flex;
  justify-content: space-between;
}
#about-body .about-txt {
  width: 70%;
}
#about-body .about-txt h3 {
  padding: 3em 3em 3em 0;
  line-height: 1.5em;
  letter-spacing: 2px;
}
#about-body .about-pic {
  width: 30%;
  display: flex;
  align-items: center;
}
#about-body .about-pic img {
  width: 100%;
  transition-duration: 250ms;
  border-radius: 5px;
}
#about-body .about-pic img:hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

@media screen and (max-width: 480px) {
  #about-body {
    flex-direction: column;
  }
  #about-body .about-txt {
    width: 100%;
  }
  #about-body .about-txt h3 {
    font-size: 1em;
    padding: 1em 0 0 0;
  }
  #about-body .about-pic {
    width: 100%;
    height: 400px;
  }
}
h2, #experience-lists {
  margin: 25px 0;
}

#experience-lists {
  display: flex;
  justify-content: space-between;
  margin-right: 25%;
}
#experience-lists h1 {
  color: lavender;
}
#experience-lists ul li {
  font-size: 1.5em;
  margin: 10px 10px 10px 25px;
}

#work-cards {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 5vh;
  justify-content: center;
}

@media screen and (max-width: 480px) {
  h2 {
    font-size: 1.25em;
  }
  #experience-lists {
    width: 100%;
    flex-direction: column;
  }
  #experience-lists .experience-list {
    margin-bottom: 3vh;
  }
  #experience-lists ul {
    margin-left: 10vw;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 15vh;
  }
  #experience-lists ul li {
    margin: 0px;
    font-size: 1.25em;
  }
  #work-cards {
    flex-direction: column;
  }
}
.work-card-wrapper {
  width: 40%;
  margin: 3%;
}

.work-card:hover {
  box-shadow: 0 0 10px turquoise;
}

.work-card {
  position: relative;
  padding: 7%;
  width: 100%;
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -15px;
  background-color: rgb(23, 42, 69);
  transition: box-shadow 0.3s ease-in-out;
  transition-duration: 0.25s;
  border-radius: 15px;
}
.work-card .work-position {
  color: lavender;
  margin: 10px 0;
}
.work-card .work-company {
  color: turquoise;
  margin: 10px 0;
}
.work-card .work-duration {
  margin: 10px 0;
}
.work-card ul {
  margin-top: 20px;
}
.work-card ul li {
  margin: 20px 0;
}

@media screen and (max-width: 480px) {
  .work-card-wrapper {
    width: 100%;
  }
}
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  height: auto;
}

.project-section {
  margin-top: 25px;
}

@media screen and (max-width: 480px) {
  .projects {
    flex-direction: column;
  }
}
.project-block {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: auto;
}

.project-card {
  width: 50%;
  display: inline-flex;
  justify-content: center;
}

.project-image {
  transition-duration: 250ms;
  margin-left: 30px;
  margin-right: 30px;
  margin-top: 5%;
  margin-bottom: 5%;
}

.project-image:hover {
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

.project.block {
  width: 85%;
}

.project.card {
  width: 70%;
}

.project-connector {
  width: 10%;
}

.project:hover {
  box-shadow: 0 0 10px turquoise;
}

.project {
  height: auto;
  padding: 25px;
  margin: 30px;
  transition: 0.25s;
  display: inline-flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-around;
  align-items: center;
  background-color: rgb(23, 42, 69);
  box-shadow: rgba(2, 12, 27, 0.7) 0px 10px 30px -15px;
  transition: box-shadow 0.3s ease-in-out;
  border-radius: 15px;
}
.project .project-header {
  margin: 10px 0;
  text-align: left;
  width: 100%;
}
.project .project-header a {
  float: right;
}
.project .project-header svg {
  height: 35px;
  width: 35px;
  background-size: 35px 35px;
}
.project .project-header svg path {
  fill: rgb(136, 146, 176);
}
.project .project-external-link:hover path {
  fill: turquoise;
}
.project .project-title {
  color: lavender;
}
.project .project-description {
  margin: 10px 0;
  text-align: left;
}
.project .project-information {
  margin: 10px 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  text-align: left;
  font-size: 0.9em;
}
.project .project-information span {
  margin-right: 15px;
}

@media screen and (max-width: 480px) {
  .project-card {
    width: 100%;
  }
  .project-block {
    flex-direction: column;
  }
  .project.card {
    width: 100%;
  }
  .project.block {
    width: 100%;
  }
  .project {
    margin: 3vh 0;
  }
  .project-image {
    width: 90vw;
  }
  .project-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;
  }
}
#contact-body {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

#contact-immediate {
  width: 40%;
}

#contact-information {
  width: 40%;
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  line-height: 2em;
  font-size: 1.25em;
}
#contact-information a {
  color: turquoise;
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.input-field {
  position: relative;
  margin-top: 35px;
  padding: 20px 0;
  color: rgb(136, 146, 176);
  width: 100%;
}
.input-field label {
  position: absolute;
  left: 10px;
  top: 10px;
  transition: 0.25s;
  font-weight: normal;
}
.input-field input, .input-field textarea {
  position: absolute;
  top: 0;
  left: 0;
  display: inline;
  outline: none;
  border: none;
  border-bottom: thin solid black;
  padding-left: 10px;
}
.input-field input {
  height: 100%;
  width: 100%;
}
.input-field input:valid ~ label,
.input-field input:focus ~ label,
.input-field textarea:valid ~ label,
.input-field textarea:focus ~ label {
  top: -25px;
  color: turquoise;
}

.input-submit {
  width: 100px;
}

#message {
  height: 30vh;
  width: 100%;
}
#message textarea {
  resize: none;
  height: 100%;
  width: 100%;
}

#contact-send {
  margin: 25px auto auto;
  width: 100px;
}

@media screen and (max-width: 480px) {
  #contact-body {
    flex-direction: column;
  }
  #contact-immediate {
    width: 100%;
  }
  #contact-information {
    width: 100%;
    font-size: 1em;
    margin: 3vh 0;
  }
}
.terminal {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    overflow-y: auto;
    overflow-x: hidden;
    cursor: text;
    background-color: #2f3640;
    letter-spacing: 1px;
}

.terminal-content{
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    height: 100%;
    font-size: 15px;
    font-family: monospace;
}

.terminal-text{
    line-height: 21px;
    color: silver;
}

.terminal-input-area{
    display: inline-flex;
    width: 100%;
    line-height: 22px;
}

.terminal-prompt{
    font-size: 15px;
    color: sandybrown;
    font-family: monospace;
    padding-right: 7px;
}

.terminal-input{
    border: 0px;
    margin: 0px;
    flex-grow: 100;
    width: 100%;
    background: transparent;
    font-size: 15px;
    color: rgb(240, 191, 129);
    font-family: monospace;
    outline: none;
    letter-spacing: 1px;
}

.terminal-ls{
    display: flex;
    flex-direction: row;
}

.terminal-command{
    color: rgb(240, 191, 129);
}

.terminal-directory{
    color: mediumpurple;
    font-weight: bold;
    padding-right: 20px;
}

.terminal-file{
    color: turquoise;
    padding-right: 20px;
}

.terminal-link{
    color: silver;
    text-decoration: underline;
}

.terminal-usage{
    padding: 0;
}
.interactive-page {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.interactive-terminal {
  width: 50vw;
  height: 50vh;
}
